table.personalinfo {
  width: auto;
  margin: 0px;
  border-top: 1px solid $color-border2;
  tbody {
    tr, tr:nth-child(2n+1) {
      background-color: transparent;
      border: 0px;
      td {
        border-left: 1px solid $color-border2;
        line-height: 1em;
        padding: 0px 10px;
      }
      td:first-of-type{
        border-left: 0px;
        padding-left: 0px;
      }
    }
  }
}


table.skills, table.resume, table.education{
  thead {
    display: none;
  }
  p{
    margin: 5px 0px;
    line-height: 1em;
  }
  tr{
    td{
      padding: 0.2em 0.75em;
    }
    td:first-of-type{
      width: 110px;
    }
  }
}
table.resume{
  tbody{
    tr, tr:nth-child(2n + 1){
      background-color: transparent;
      border-width: 0px;
    }
    tr:first-child{
      background-color: #f2f2f2;
    }
  }
}
table.resume{
  tbody{
    tr:first-child td{
      font-weight: bold;
    }
  }
}
